.theme_wrapper .theme_container{margin:0;padding-top:3px}.theme_wrapper .theme_container .switch{position:relative;display:inline-block;width:60px;height:27px}.theme_wrapper .theme_container .switch:after{position:absolute;border-radius:50%;content:"☽";font-size:25px;height:20px;width:20px;left:42px;top:-2px}.theme_wrapper .theme_container .switch input{opacity:0;width:0;height:0}.theme_wrapper .theme_container .switch input+.slider{color:#fff;transition:.1s}.theme_wrapper .theme_container .switch input:checked+.slider{background-color:#ccc;color:#000;transition:.1s}.theme_wrapper .theme_container .switch input:focus+.slider{box-shadow:0 0 1px #ccc}.theme_wrapper .theme_container .switch input:checked+.slider:before{-webkit-transform:translateX(29px);-ms-transform:translateX(29px);transform:translateX(33px)}.theme_wrapper .theme_container .switch .slider{position:absolute;cursor:pointer;border-radius:33px;top:0;left:0;right:0;bottom:0;background-color:#ccc}.theme_wrapper .theme_container .switch .slider:before{position:absolute;border-radius:50%;content:"";height:23px;width:23px;left:2px;top:2px;background-color:#fff;-webkit-transition:.3s;transition:.3s}.theme_wrapper .theme_container .switch .slider:after{position:absolute;border-radius:50%;content:"☀";font-size:25px;height:20px;width:25px;left:3px;top:-2px}